import defaultsDeep from 'lodash/defaultsDeep'
import DefaultOptions from './image-resize-defaults.js'
import {Resize, DisplaySize} from './image-resize-modules.js'
const knownModules = { Resize, DisplaySize }


function isDataURL(s) {
  let dataUrlRegex = /^\s*data:([a-z]+\/[a-z]+(;[a-z\-]+\=[a-z\-]+)?)?(;base64)?,[a-z0-9\!\$\&\'\,\(\)\*\+\,\;\=\-\.\_\~\:\@\/\?\%\s]*\s*$/i
  return !!s.match(dataUrlRegex)
}


/**
 * Custom module for quilljs to allow user to resize <img> elements
 * @see https://quilljs.com/blog/building-a-custom-module/
 */
export default class ImageResize {
  constructor(quill, options={}) {
    this.quill = quill

    // Apply the options to our defaults, and stash them for later
    // defaultsDeep doesn't do arrays as you'd expect, so we'll need to apply the classes array from options separately
    let moduleClasses = false
    if (options.modules) {
      moduleClasses = options.modules.slice()
    }
    this.options = defaultsDeep({}, options, DefaultOptions)
    if (moduleClasses !== false)
      this.options.modules = moduleClasses

    // disable native image resizing on firefox
    document.execCommand('enableObjectResizing', false, 'false')

    // 
    this._handleClick = this.handleClick.bind(this)
    this.quill.root.addEventListener('click', this._handleClick, false)

    this.quill.root.parentNode.style.position = this.quill.root.parentNode.style.position || 'relative'

    // setup modules
    this.moduleClasses = this.options.modules
    this.modules = []
  }

  initializeModules() {
    this.removeModules()

    for (let ModuleClass of this.moduleClasses) {
      this.modules.push(new(knownModules[ModuleClass] || ModuleClass)(this))
    }
    for (let module of this.modules)
      module.onCreate()

    this.onUpdate()
  }

  onUpdate() {
    this.repositionElements()
    for (let module of this.modules) {
      module.onUpdate()
    }
    this.quill.updateContents()
  }

  removeModules() {
    for (let module of this.modules)
      module.onDestroy()
    this.modules = []
  }

  handleClick() {
    let t = event.target
    if (t && t.tagName && t.tagName.toUpperCase() === 'IMG' && !isDataURL(t.src)) {
      if (this.img === event.target)
        return
      if (this.img) {
        this.hide()
      }
      this.show(event.target)
    } else if (this.img) {
      this.hide()
    }
  }

  show(img) {
    this.img = img
    this.showOverlay()
    this.initializeModules()
  }

  showOverlay() {
    if (this.overlay)
      this.hideOverlay()

    this.quill.setSelection(null)
    this.setUserSelect('none')

    this._checkImage = this.checkImage.bind(this)
    document.addEventListener('keyup', this._checkImage, true)
    this.quill.root.addEventListener('input', this._checkImage, true)

    this.overlay = document.createElement('div')
    Object.assign(this.overlay.style, this.options.overlayStyles)

    this.quill.root.parentNode.appendChild(this.overlay)
    this.repositionElements()
  }

  hideOverlay() {
    if (!this.overlay)
      return

    this.quill.root.parentNode.removeChild(this.overlay)
    this.overlay = undefined

    // 
    document.removeEventListener('keyup', this._checkImage)
    this.quill.root.removeEventListener('input', this._checkImage)

    this.setUserSelect('')
  }

  repositionElements() {
    if (!this.overlay || !this.img)
      return

    const parent = this.quill.root.parentNode
    const imgRect = this.img.getBoundingClientRect()
    const containerRect = parent.getBoundingClientRect()

    Object.assign(this.overlay.style, {
      left: `${imgRect.left - containerRect.left - 1 + parent.scrollLeft}px`,
      top: `${imgRect.top - containerRect.top + parent.scrollTop}px`,
      width: `${imgRect.width}px`,
      height: `${imgRect.height}px`,
    })
  }

  hide() {
    this.hideOverlay()
    this.removeModules()
    this.img = undefined
  }

  setUserSelect(value) {
    // set on contenteditable element and <html>
    this.quill.root.style.userSelect = value
    document.documentElement.style.userSelect = value
  }

  checkImage(evt) {
    if (this.img) {
      if (evt.keyCode == 46 || evt.keyCode == 8) {
        // delete|backspace key
        window.Quill.find(this.img).deleteAt(0)
      }
      this.hide()
    }
  }
}



